<template>

    <v-container > 
        <v-layout
        justify-center
        >
        <v-card class="elevation-12">
            <v-toolbar
                color="primary"
                dark
                flat
              > 
              <v-toolbar-title ></v-toolbar-title>
            </v-toolbar>
        
        <v-card-text>
            <v-form
            ref="form"
            v-model="valid"
            lazy-validation>
                <v-text-field
                v-model="username"
                :rules="usernameRules"
                label=""
                required>
                </v-text-field>

                <v-text-field
                v-model="password"
                :append-icon="showpassword ? 'visibility' : 'visibility_off'"
                :rules="passwordRules"
                :type="showpassword ? 'text' : 'password'"
                label=""
                required
                @click:append="showpassword = !showpassword"
            ></v-text-field>

            <v-text-field
                v-model="checkpassword"
                :append-icon="showpassword ? 'visibility' : 'visibility_off'"
                :rules="checkpasswordRules"
                :type="showpassword ? 'text' : 'password'"
                label=""
                required
                @click:append="showpassword = !showpassword"
            ></v-text-field>

            <v-text-field
                v-model="email"
                :rules="emailRules"
                label=""
                required
            ></v-text-field>
            <v-btn
            color="primary"
            class="mr-4"
            @click="SendEmailCode"></v-btn>

            <v-text-field
                v-model="emailcode"
                :rules="emailcodeRules"
                label=""
                required
            ></v-text-field>

               
            </v-form>

        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            
            <v-btn
            color="primary"
            class="mr-4"
            @click="SubmitRegister">
            </v-btn>

            <v-btn
            :disabled="!valid"
            color="primary"
            class="mr-4"
            @click="GoLogin">>>
            </v-btn>
        </v-card-actions>
            
        </v-card>
        </v-layout>
    </v-container>

</template>

<script>
import md5 from 'js-md5';
export default {
    name:"Register",
    data:()=>({
        valid: true,
        showpassword:false,
        username:"",
        usernameRules: [
        v => !!v || '!',
        v => (v && v.length >= 6) || '6',
        ],
        password:"",
        passwordRules:[
            v=> !! v || "!",
            v => (v && v.length >= 6) || '6',
        ],
        checkpassword:"",
        checkpasswordRules:[
            v=> !! v || "!",
        ],
        email:"",
        emailRules:[
            v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || ''
        ],
        emailcode:"",
        emailcodeRules:[
            v=> !! v || "!",
        ],
        

    }),
    methods:{
        // 
        GoLogin(){
            this.$router.push("/user/login")
        },
        
        // 
        SubmitRegister(){
           let params ={username:this.username,password:md5(this.password),piccode:this.piccode,email:this.email,emailcode:this.emailcode };
            console.log("API,",params);
            this.$store.dispatch('user/register',params);
        },
        // 
        SendEmailCode(){
            this.$store.dispatch('user/send_email_code',{mailto:this.email});
        },
    },
    computed:{

    },
}
</script>