<template>
    <v-container >
        <v-layout justify-center>
        <v-card class="elevation-12">
            <v-toolbar
                color="primary"
                dark
                flat
              > 
              <v-toolbar-title ></v-toolbar-title>
            </v-toolbar>
        
        <v-card-text>
            <v-form
            ref="form"
            v-model="valid"
            lazy-validation>
                <v-text-field
                v-model="username"
                :rules="usernameRules"
                label=""
                required>
                </v-text-field>

                <v-text-field
                v-model="password"
                :append-icon="showpassword ? 'visibility' : 'visibility_off'"
                :rules="passwordRules"
                :type="showpassword ? 'text' : 'password'"
                label=""
                required
                @click:append="showpassword = !showpassword"
            ></v-text-field>
               
            </v-form>

        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
            :disabled="!valid"
            color="primary"
            class="mr-4"
            @click="SubmitLogin">

            </v-btn>
            <v-btn
            color="primary"
            class="mr-4"
            @click="GoRegister">
            </v-btn>

            <v-btn
            color="primary"
            class="mr-4"
            @click="GoForgetPassword">

            </v-btn>
        </v-card-actions>
            
        </v-card>
        </v-layout>
    </v-container>

</template>

<script>
import md5 from 'js-md5';
export default {
    name:"Login",
    data:()=>({
        valid: true,
        showpassword:false,
        username:"",
        usernameRules: [
        v => !!v || '!',
        v => (v && v.length >= 6) || '6',
        ],
        password:"",
        passwordRules:[
            v=> !! v || "!",
            v => (v && v.length >= 6) || '6',
        ],
        piccode:"",
        piccodeRules:[
            v=> !! v || "!",
        ],
    }),
    methods:{
        //  
        SubmitLogin(){
            let params ={username:this.username,password:md5(this.password),piccode:this.piccode };
            console.log("API,",params);
            this.$store.dispatch('user/login',params);
        },
        // 
        GoRegister(){
            // console.log("");
            // this.$router.push(types.ROUTE_REGISTER);
            this.$router.push("/user/register");
        },
        // 
        GoForgetPassword(){
            // console.log("");
            // this.$router.push(types.ROUTE_RESETPASSWORD);
            this.$store.dispatch("user/update_userinfo");
            
        }

    },
    computed:{

    }
}
</script>