import { make } from 'vuex-pathify'
// import * as api from "@/apis/webapi"
import * as api from "@/apis/wswebapi"

const state = {
    islogin:false,
    // 
    userinfo:{},
    trxaddress:"",
}

const mutations = make.mutations(state)

const actions = {
    ...make.actions('userinfo'),
    ...make.actions('islogin'),
    ...make.actions('trxaddress'),
    ...make.actions('testlogin'),
    // 
    update_loginstatus({dispatch}){
        api.GetLoginStatus().then(res=>{
            if (res.code==200){
                console.log("",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
            }else{
                console.log("",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 
    update_userinfo({dispatch}){
        api.GetUserInfo().then(res=>{
            if (res.code==200){
                console.log("",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                dispatch("setUserinfo",res.data);
            }else{
                console.log("",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 
    login({dispatch},payload){
        api.Login(payload).then(res=>{
            if (res.code==200){
                console.log("",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                dispatch("update_userinfo");
                dispatch("setIslogin",true);
                dispatch("update_trxaddress");
                
            }else{
                console.log("",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 
    logout({dispatch}){
        api.Logout();
        dispatch("setIslogin",false);
        dispatch("setUserinfo",{});
    },
    // 
    send_email_code({dispatch},payload){
        api.GetEmailcode(payload).then(res=>{
            if (res.code==200){
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
            }else{
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true});
            }
        })
    },
    // 
    register({dispatch},payload){
        api.Register(payload).then(res=>{
            if (res.code==200){
                console.log("",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                dispatch("setIslogin",true);
            }else{
                console.log("",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 
    resetpassword({dispatch},payload){
        api.ResetPassword(payload).then(res=>{
            if (res.code==200){
                console.log("",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                dispatch("setIslogin",true);
            }else{
                console.log("",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // trxaddress
    update_trxaddress({dispatch}){
        if (state.trxaddress==""){
            api.GetTrxAddress().then(res=>{
                if (res.code==200){
                    dispatch("setTrxaddress",res.data);
                }else{
                    console.log(res);
                }
            })
        }
        
    }
}

const getters = {
    // 
    collections:state=>{
        if (state.userinfo.collectionauth){
            
            let keys=Object.keys(state.userinfo.collectionauth);
            let cols =keys.map(item=>({
                text:item,value:item,
            }))
        
            return cols;
        }else{
            return [];
        }
    },
    username:state=>{
        return state.userinfo.username;
    },
    email:state=>{
        return state.userinfo.email;
    },
    trxaddress:state=>{
        return state.trxaddress;
    },
    trxaddressqrcode:state=>{
        return "http://cdn.oldking.club:2086/v1/web/generateQrcode?text="+state.trxaddress;
    },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}
